html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

#scene-container {
  width: 100vw;
  height: 100vh;
}

canvas {
  display: block;
}

.info-panel {
  color: #fff;
  z-index: 10;
  font-family: monospace;
  position: absolute;
  top: 10px;
  left: 10px;
}

@media (width <= 768px) {
  .lil-gui {
    max-height: 50vh;
    overflow-y: auto;
    transform: translateX(-50%);
    width: 90% !important;
    top: 5px !important;
    left: 50% !important;
    right: 5% !important;
  }

  div.text-overlay div {
    font-size: 16px !important;
  }
}
